import React, { useState, useEffect } from "react";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import Card from "@vkontakte/vkui/dist/components/Card/Card";

import {
  View,
  PanelHeaderContext,
  List,
  Cell,
  Separator,
  SimpleCell,
  PanelHeaderBack
} from "@vkontakte/vkui";

import "./Shop.css";

import CardGrid from "@vkontakte/vkui/dist/components/CardGrid/CardGrid";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
// import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Avatar from "@vkontakte/vkui/dist/components/Avatar/Avatar";
import { Player } from "./api";

import ship from "../img/ship.jpg";
import cat from "../img/cat.jpg";
import duck from "../img/duck.jpg";

let playersList = [];
let nowUser;
let setModali;


function generateList(container) {
  if (typeof container == "string") return "";
  playersList = [];
  let param;
  let isIncludes = false;
  for (const player of container) {
    param = Object.keys(player)[2];
    if (player.nick == nowUser.nick) isIncludes = true;
    playersList.push(
      <div key={container.indexOf(player)}>
        <SimpleCell
          name={player.nick}
          avatarka = {player.avatar}
          readydisc = {`${Object.keys(player)[2]}: ${
            Object.values(player)[2]
          }`}
          onClick={function(){
            setModali({
              avatar: <Avatar src={{ cat, ship, duck }[this.avatarka]} />,
              header: this.name,
              now: 'persInfo',
              content: <>
              <p>{this.readydisc}</p>
              <Button onClick={()=>{}} mode='outline'  >Узнать страницу вк</Button>
              </>
            })
          }
          }
          style={{
            textAlign: "center",
          }}
          before={
            <span
              style={{
                fontWeight: "bolder",
              }}
            >
              {container.indexOf(player) + 1}
            </span>
          }
          after={<Avatar src={{ cat, ship, duck }[player.avatar]} />}
          description={`    ${Object.keys(player)[2]}: ${
            Object.values(player)[2]
          }`}
        >
          {player.nick}
        </SimpleCell>
        <Separator />
      </div>
    );
  }
  if (!isIncludes) {
    playersList.push(
      <div key={21}>
        <SimpleCell
          disabled
          style={{
            textAlign: "center",
          }}
          before={
            <span
              style={{
                fontWeight: "bolder",
              }}
            >
              {"Ты"}
            </span>
          }
          after={<Avatar src={{ cat, ship, duck }[nowUser.avatar]} />}
          description={`    ${param}: ${
            typeof nowUser[param] != "object"
              ? nowUser[param]
              : nowUser[param].lvl
          }`}
        >
          {nowUser.nick}
        </SimpleCell>
        <Separator />
      </div>
    );
  }
}

let firstTime = true;

const Top = ({ id, user, topInfo,setModal , go }) => {
  setModali=setModal;
  nowUser = user;
  useEffect(() => {
    // setTimeout(()=>{
    //   console.log('yeee');
    // },2000)
  }, []);
  const [isVariant, setVariants] = useState(true);
  const [head, setHeader] = useState("Уровень ⬇");
  console.log(topInfo, "!!!");
  if (firstTime && topInfo) {
    firstTime = false;
    generateList(topInfo.lvl);
  }

  return (
    <Panel id={id}>
      <PanelHeader
        left={<PanelHeaderBack onClick={()=>go('home') } />}
        onClick={() => setVariants(true)}
      >
        {head}
      </PanelHeader>
      <PanelHeaderContext opened={isVariant}>
        <List
          style={{
            textAlign: "center",
          }}
        >
          <Cell
          // asideContent={'ok'}
            onClick={() => {
              setHeader("Уровень ⬇");
              generateList(topInfo.lvl);
              setVariants(false);
            }}
          >
            Уровень
          </Cell>
          <Separator />
          <Cell
            onClick={() => {
              setHeader("CP2S ⬇");

              generateList(topInfo.maxCP2S);
              setVariants(false);
            }}
          >
            CP2S
          </Cell>
          <Separator />

          <Cell
            onClick={() => {
              setHeader("Баланс ⬇");

              generateList(topInfo.balance);
              setVariants(false);
            }}
          >
            Коины
          </Cell>

        </List>
      </PanelHeaderContext>
      {playersList}
    </Panel>
  );
};

export default Top;
