import axios from "axios";
import React, { useState } from "react";
// import persik from '../img/persik.png';
import { Alert, Input, FormLayout, ScreenSpinner } from "@vkontakte/vkui";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
import Card from "@vkontakte/vkui/dist/components/Card/Card";
import { connectServer } from "./api";

// import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from "@vkontakte/vkui/dist/components/Div/Div";

import "./Intro.css";
import Ship from "../img/ship.jpg";
import Cat from "../img/cat.jpg";
import Duck from "../img/duck.jpg";
// import { setTimeout } from "core-js";

const itemStyle = {
  // flexShrink: 0,
  // width: 80,
  // height: 94,
  display: "flex",
  flexDirection: "column",
  // alignItems: 'center'
};

let userInfo = {
  avatar: "duck",
};

const onChoose = (e) => {
  userInfo.avatar = e.target.className.split("avatar ")[1];
  const avatars = document.getElementsByClassName("avatar");
  for (const avatar of avatars) {
    if (avatar.className !== e.target.className) {
      avatar.style.borderTop = "";
      avatar.style.marginBottom = "";
    }
  }
  e.target.style.borderTop = "6px solid #4986cc";
  e.target.style.marginBottom = "-10px";
};

const avka = (src) => (
  <img
    onClick={onChoose}
    className={`avatar ${src.split("a/")[1].split(".")[0]}`}
    src={src}
    alt="avka"
  />
);

// async function
let areHidden = true;

const Intro = ({ id, go, user, setUser }) => {
  const [userNick, setNick] = useState("");
  const [isError, makeError] = useState("");
  const [spinner, isSpinner] = useState("");
  const [betaAlert, removeBeta] = useState(
    <Alert
      style={{
        marginTop: "50%",
      }}
      actions={[
        {
          title: "Ok",
          autoclose: true,
          mode: "cancel",
        },
      ]}
      onClose={() => {
        areHidden = false;
        removeBeta("");
      }}
    >
      {" "}
      <h1>Бета Версия</h1>
      <p>
        Это бета версия, игра нестабильна. Пожалуйста, помогите нам, отправляя
        найденные ошибки в лс группы
      </p>
    </Alert>
  );

  const isOkay = () => {
    return (
      userNick.length > 2 &&
      userNick.length < 15 &&
      /^[а-яА-ЯёЁa-zA-Z0-9][а-яА-ЯёЁa-zA-Z0-9-_.]{1,15}$/.test(userNick)
    );
  };
  const register = async function (e) {
    console.log(
      /^[а-яА-ЯёЁa-zA-Z0-9][а-яА-ЯёЁa-zA-Z0-9-_.]{1,20}$/.test(userNick)
    );
    areHidden = true;
    isSpinner(<ScreenSpinner />);

    let start = Date.now();
    let regRequest = await axios.post("https://lucky-coin.ru/api/createUser", {
      nick: userNick,
      avatar: userInfo.avatar,
    });
    areHidden = true;

    isSpinner("");

    console.log(Date.now() - start);

    const { data } = regRequest;
    console.log(data);
    if (data.type === "error") {
      console.log("Error happened");
      makeError(
        <Alert
          actions={[
            {
              title: "Отмена",
              autoclose: true,
              mode: "cancel",
            },
          ]}
          onClose={() => {
            areHidden = false;

            setNick("");
            makeError("");
          }}
        >
          {" "}
          <h1>{data.text}</h1>{" "}
        </Alert>
      );
      return;
    }
    console.log(this["data-to"]);

    setUser({
      id: 1,
      nick: userNick,
      avatar: userInfo.avatar,
      balance: 0,
      donate: 0,
      clicks: 0,
      mining: 0,
      maxCP2S: 0,
      lvl: {
        lvl: 1,
        exp: 0,
      },
      ban: {
        is: false,
        reason: "Автокликер",
      },
    });
    connectServer({
      id: 1,
      nick: userNick,
      avatar: userInfo.avatar,
      balance: 0,
      donate: 0,
      clicks: 0,
      mining: 0,
      maxCP2S: 0,
      lvl: {
        lvl: 1,
        exp: 0,
      },
      ban: {
        is: false,
        reason: "Автокликер",
      },
      items: {
        mining: [],
        clicks: [],
        abilities: [],
      },
    });
    go("home");

    // setTimeout( () => {

    //     console.log( this );
    //     go( this[ 'data-to' ] )
    // }, 2000 );
  };

  return (
    <Panel id={id}>
      <PanelHeader style={{ opacity: areHidden ? 0.4 : 1 }}>
        {" "}
        Привет, {user ? user.name : " новичок"}{" "}
      </PanelHeader>
      {betaAlert}

      <Card style={{ opacity: areHidden ? 0.4 : 1 }} size="m" mode="tint">
        <h4>Выбери свою аватарку </h4>
      </Card>

      <Group title="Navigation Example">
        <Div>
          {/* <Button size="xl" level="3" onClick={go} data-to="persik">
                    Читать
				</Button> */}
        </Div>
      </Group>

      {/* <HorizontalScroll> */}
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <div style={{ ...itemStyle, paddingRight: 20, paddingLeft: 10 }}>
          {avka(Ship)}
          {/* <Avatar size={64} style={{ marginBottom: 8 }}> {avka( Ship )}</Avatar> */}
        </div>
        <div style={{ ...itemStyle, paddingRight: 20 }}>
          {avka(Duck)}
          {/* <Avatar size={64} style={{ marginBottom: 8 }}>{avka( Cat )} </Avatar> */}
        </div>
        <div style={{ ...itemStyle, paddingRight: 20 }}>
          {avka(Cat)}
          {/* <Avatar size={64} style={{ marginBottom: 8 }}>{avka( Cat )} </Avatar> */}
        </div>
      </div>
      {/* </HorizontalScroll> */}

      {isError}
      <Card
        style={{ marginTop: 20, opacity: areHidden ? 0.4 : 1 }}
        size="m"
        mode="tint"
      >
        <h4>Придумай себе ник</h4>
      </Card>
      {spinner}
      <FormLayout style={{ opacity: areHidden ? 0.4 : 1 }}>
        <Input
          type="nick"
          bottom={
            isOkay()
              ? "Отлично! Жми на кнопку"
              : "Ник от 3 до 15 символов Только буквы и цифры"
          }
          value={userNick}
          status={isOkay() ? "valid" : "error"}
          onChange={(e) => setNick(e.currentTarget.value.trim())}
        />
      </FormLayout>

      <p style={{ opacity: 0.6, display: isOkay() ? "" : "none" }}>
        P.S. Оскорбительные ники будут заблокированы!
      </p>

      <Button
        style={{ marginTop: 10, display: isOkay() ? "" : "none" }}
        size="xl"
        level="3"
        onClick={isOkay() ? register : null}
        data-to="home"
      >
        Регистрация
      </Button>
    </Panel>
  );
};

export default Intro;
