import openSocket from "socket.io-client";

import React from "react";

import { Snackbar } from "@vkontakte/vkui";
import { updateShop } from "./shopItems";

let isErrored = false;
class Connection {
  constructor() {
    this.actions = new Object();
  }

  connect(user) {
    //this.socket = openSocket("https://lucky-coin.ru:443");
    this.socket = openSocket({
      url: `https://lucky-coin.ru:443`,
      headers: {
        "Content-Type": "application/json",
      },
    });

    this.msgHandler();
    this.socket.emit("init", user);
    setTimeout(() => this.socket.emit("updateUser"), 1);
  }

  isConnected() {
    return this.socket ? true : false;
  }

  msgHandler() {
    this.socket.on("connect", () => {
      this.socket.on("message", (msg) => {
        console.log(msg);
        if (msg.type == "error") {
          isErrored = true;
          switch (msg.result) {
            case "Already logged":
              this.actions.setError("Ты уже зашёл в игру с другого устройства");
              this.actions.go("error");
              this.socket.disconnect();
              break;

            case "looseCon":
              this.actions.setError(
                "Вы были отключены за неактив, очистите кеш и перезайдите"
              );
              this.actions.go("error");
              this.socket.disconnect();

              break;

            default:
              console.log("closin");
              this.actions["go"]("error");
              this.socket.disconnect();
              break;
          }
        }
        if (msg.type == "updateUser") {
          this.actions.updateUser(msg.result);
          updateShop(msg.result);
          return;
        }

        if (msg.type == "newLvl") {
          this.actions.gameSnack(
            <Snackbar onClose={() => this.actions.gameSnack(null)}>
              Новый уровень
            </Snackbar>
          );
          return;
        }

        if (msg.type == "updateTop") {
          this.actions.updateTop(msg.result);
        }
      });
    });

    this.socket.on("disconnect", () => {
      if (isErrored) return;
      this.socket.disconnect();
      this.actions.setError(
        "Разорвано соединение. Может вас долго не было? Перезайдите"
      );
      this.actions.go("error");
    });
  }

  addAction(name, action) {
    if (this.actions[name]) return;
    console.log("new Action", name);
    this.actions[name] = action;
  }

  makeClick(user) {
    this.socket.emit("click", user);
  }

  getTop() {
    this.socket.emit("getTop");
  }
}

const Player = new Connection();

function connectServer(user) {
  if (!Player.isConnected()) Player.connect(user);
}

