import React, { useState, useEffect } from "react";
// import bridge from '@vkontakte/vk-bridge';
import View from "@vkontakte/vkui/dist/components/View/View";
import ScreenSpinner from "@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner";
import "@vkontakte/vkui/dist/vkui.css";
import axios from "axios";

import {
  ModalPageHeader,
  ModalRoot,
  ModalCard,
  ModalPage,
  Avatar,
} from "@vkontakte/vkui";

import Home from "./panels/Home";
import Intro from "./panels/Intro";
import Clicker from "./panels/Clicker";
import Shop from "./panels/Shop";
import Top from "./panels/Top";

import MiniGames from "./panels/MiniGames";

import Error from "./panels/Error";
import { Player, connectServer } from "./panels/api";
axios.defaults.headers = {
  "x-launch-params": window.location.search,
  // referer: window.location.search,
  referrer: window.location.search,
  hash: window.location.hash,
};
async function getInfo(changePanel, go, setError) {
  let logRequest = await axios({
    method: "POST",
    url: `https://lucky-coin.ru/api/isRegistered`,
    headers: {
      "Content-Type": "application/json",
    },
  });
  //let logRequest = await axios.post("https://lucky-coin.ru/api/isRegistered");
  const { data } = logRequest;
  console.log(data.result);

  if (data.result) {
    if (data.result.ban.is) {
      setError("Блокировка аккаунта.\nПричина: " + data.result.ban.reason);

      go("error");
      return;
    }
    connectServer(data.result);

    return data.result;
  } else {
    changePanel("intro");
  }
}

const App = () => {
  const [activePanel, setActivePanel] = useState("home");
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState(<ScreenSpinner size="large" />);
  const [errorReason, setError] = useState(
    "Что-то пошло не так...\nПерезайдите?"
  );
  const [modalNow, setModal] = useState({
    now: null,
    header: "Статистика",
    content: "Content",
    avatar: false,
  });
  const [topInfo, setTopInfo] = useState(null);

  const modal = (
    <ModalRoot activeModal={modalNow.now}>
      <ModalCard
        onClose={() => setModal({ now: null, content: "Test" })}
        id="stat"
      >
        <ModalPageHeader>{modalNow.header}</ModalPageHeader>
        {modalNow.content}
      </ModalCard>

      <ModalCard
        onClose={() => setModal({ now: null, content: "Test" })}
        id="persInfo"
        icon={modalNow.avatar}
      >
        <ModalPageHeader>{modalNow.header}</ModalPageHeader>

        {modalNow.content}
      </ModalCard>
    </ModalRoot>
  );

  function logi(e) {
    console.log(e);
  }

  const go = (e) => {
    setActivePanel(typeof e == "object" ? e.currentTarget.dataset.to : e);
  };
  useEffect(() => {
    // bridge.subscribe(({ detail: { type, data }}) => {
    // 	if (type === 'VKWebAppUpdateConfig') {
    // 		const schemeAttribute = document.createAttribute('scheme');
    // 		schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
    // 		document.body.attributes.setNamedItem(schemeAttribute);
    // 	}
    // });
    async function fetchData() {
      const user = await getInfo(setActivePanel, go, setError);
      setPopout(null);
      if (!user) return;
      setUser(user);
    }
    fetchData();
  }, []);

  Player.addAction("go", go);
  Player.addAction("setError", setError);
  Player.addAction("updateUser", setUser);
  Player.addAction("updateTop", setTopInfo);

  return (
    <View
      modal={modal}
      activePanel={activePanel}
      onTransition={logi}
      popout={popout}
    >
      <Intro id="intro" user={fetchedUser} go={go} setUser={setUser} />
      <Home
        id="home"
        user={fetchedUser}
        go={go}
        setError={setError}
        setModal={setModal}
      />
      <Clicker id="clicker" setUser={setUser} user={fetchedUser} go={go} />
      <Shop id="shop" setUser={setUser} user={fetchedUser} go={go} />
      <Top
        id="top"
        topInfo={topInfo}
        setModal={setModal}
        setPopout={setPopout}
        user={fetchedUser}
        go={go}
      />
      <MiniGames id="miniGames" go={go} />

      <Error id="error" reson={errorReason} />
    </View>
  );
};

export default App;
