import React, { useState,useEffect } from "react";
import Panel from "@vkontakte/vkui/dist/components/Panel/Panel";
import PanelHeader from "@vkontakte/vkui/dist/components/PanelHeader/PanelHeader";
import Card from "@vkontakte/vkui/dist/components/Card/Card";
import "./Shop.css";


import {Cell,FixedLayout,Tabs,TabsItem,HorizontalScroll,Div,Link,ScreenSpinner} from "@vkontakte/vkui";

import CardGrid from "@vkontakte/vkui/dist/components/CardGrid/CardGrid";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
// import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Avatar from "@vkontakte/vkui/dist/components/Avatar/Avatar";
import {Player} from "./api";
import Fish from "../img/fish.png";
import {  miningOfflinetems} from './shopItems'

function kRound(number){
  if(number<=1e3) return Math.round(((number) + Number.EPSILON) * 100) / 100;
  if(number>999 && number<1e6) return Math.round(((number/1e3) + Number.EPSILON) * 10) / 10+' к';
  return  Math.round(((number/1e6) + Number.EPSILON) * 100) / 100   +' кк'
};

let mainContent = [];


function generateList(name,user){
  mainContent=[];
  const routers = {
    mining: miningOfflinetems()
  }
  const itemsArray = routers[name];
  for (const item of itemsArray) {
    mainContent.push(
    <Cell
    key={itemsArray.indexOf(item)}
    before={item.icon}
    asideContent={`[ ${item.amount} ]`}
    indicator={
      item.mining+' fish/s'
    }

    style={{
      // borderTop: '0.1px solid rgba(0,0,0,0.3)',
      marginTop: '15px',
      borderRadius: '10%',
      boxShadow: '1px 5px 5px rgba(0,0,0,0.2)',
      padding: 0

    }}
    description={
  
      <Div  className="">
      <Div
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "row",
          justifyContent: "start",
          textAlign: 'left',
          // marginBottom: '2%',
          margin: 0,
          padding: 0,
          fontWeight: 'bolder',
          fontSize: 'large',
          color: user.balance>=item.price ?'green': 'red'
        }}
      >
        {item.price}
        <Div className="iconPrice" style={{ marginLeft: 8 }} />
      </Div>
      <Div className="">
        <Link id={itemsArray.indexOf(item)} onClick={''}>
          Подробнее
        </Link>
      </Div>
    </Div>

   

      }>
      {item.name}
    </Cell>
    )
    
  }
  return mainContent;

}



const Shop = ({ id, user, setUser,go }) => {

  const [activeTab,setTab] = useState('mining');

  function moveTab(name){
    setTab(name);
    window.scrollTo(0, 0);
  }


  function getUser(param) {

    if (!user) return "";
    // if(param=='avatar') return {cat,ship,duck}[user.avatar]
      return user[param];
    
  }



  return (
    <Panel  id={id}>
      <PanelHeader  onClick={()=>go('home')}     left={
         <div className='shopContainer'>
          
          <span className='headerDonate' >{kRound(getUser('donate'))}</span>
          {/* <div className='iconDonateHead'></div> */}
          <Div className="iconDonateHead" style={{ marginLeft: 8 }} />

          </div>

         } 
         > 
         <div className='shopContainer'>
         <span  className='shopBalance' >{kRound(getUser('balance'))}</span>
         {/* <img  src={Fish} className='shopBalanceIcon'></img> */}
        <Div className="iconFishHead" style={{ marginLeft: 8 }} />

         </div>
         </PanelHeader>
         <FixedLayout vertical="top" filled={true}>
            <Tabs>
            <HorizontalScroll>
              <TabsItem name='mining'  onClick={function(){moveTab(this.name)}} selected={activeTab === "mining"}>
                Майнинг
              </TabsItem>
              <TabsItem name='clicks'  onClick={function(){moveTab(this.name)}}selected={activeTab === "clicks"}>
                Клики
              </TabsItem>
              <TabsItem
                name='abilities'  onClick={function(){moveTab(this.name)}}
                selected={activeTab === "abilities"}
              >
                Возможности
              </TabsItem>

              
              </HorizontalScroll>
            </Tabs>
          </FixedLayout>
          
          <Div 
          style={{ marginBottom: 50 }}>
          </Div>
          


          {generateList(activeTab,user)}

   
    </Panel>
  );
};

export default Shop;
