import Icon24Work from '@vkontakte/icons/dist/24/work';
import React from "react";
import {Player} from "./api";

let locUser ={
    items: {
        mining: [],
        clicks: [],
        abilities: []
    }
};


function updateShop(user){
    console.log('updatingShop');
    if(locUser!=user) locUser=user;
}


const miningOfflinetems = (locUser) =>{
    return [
    {
        name: 'Майнер1',
        price: 1,
        mining: 0.001,
        icon: <Icon24Work/>,
        amoutnt: 0
    },
    {
        name: 'Майнер2',
        price: 2,
        mining: 0.002,
        icon: <Icon24Work/>,
        amount: 0


    },
    {
        name: 'Майнер3',
        price: 3,
        mining: 0.003,
        icon: <Icon24Work/>,
        amount: 0


    },
].map(item=>{
    if(!locUser.mining.length)return item;
    if(locUser.mining.map(itm=>itm.name).includes(item.name)){
        item.amount = locUser.mining.find(itm=>itm.name==item.name).amount
    }
    return item
})

}


export {miningOfflinetems,updateShop};